@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  
  font-family: "Helvetica, sans-serif";
}

.blogDetails-container{
  padding: 15px;
}

.blogDetails-container image {
  width: 70%;
  height: 200px;
}

.newsApi{
  padding: 5px;
  text-align: left;
}



.weatherApi h4{
  padding-top: 5px;
}

.twitterApi{
  margin: 15px;
}


.nav-heading{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  background-color: red;
  color: white;
  margin: 10px;
  border-radius: 5px;
  padding-bottom: 10px;
}

.blogDetails-container{
  background-color: silver;
  margin-top: 10px;
  height: 70px;
  padding: 25px;
  border-radius: 5px;
}

.blogDetails-container img{
  width: 70%;
}

.blogDetails-container h3{
  padding-top: 10px;
  font-size: 18px;
  line-height: 1.4;
}


.App {
  text-align: center;
}


.blog-container{
  background-color: wheat;
  width: 100%;
}


.App-leftdiv{
  display: flex;
  flex-direction: column;
  color: #000;
  width: 75%;
  height: 680px;
  background-color: #edebeb;
  float: left;
  } 


.App-header {
  background-color: black;
  background-image: url(/static/media/v975-background-07-b-kqkr4ukm.db327abe.jpg);
  display: flex;
  justify-content: space-around;
  width: 100%;
  flex-direction: column;  
  color: black;
  padding-top: 5px;
  padding-bottom: 5px;
}




  .App-leftdiv h1{
    color: #1a48c4;
  }

  .App-leftdiv a{
    text-decoration: none;
  }


.App-rightdiv{
  display: flex;
  color: white;
  width: 25%;
  height: 680px;
  background-color: #edebeb;
  flex-direction: column;
}

.App-footer{
  background-color: black;
  padding-top: 15px;
  padding-bottom: 15px;
  color: white;
  display: flex;
  width: 100%;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  height: auto;
}

.App-link {
  color: #61dafb;
}

.links{
  display: flex;
  justify-content: space-around;
}

.links a{
  text-decoration: none;
  color: white;
}

.links a:hover{
  color:yellow;
}

.post-container h4{
  padding: 5px;
  padding-top: 10px;
  text-align: left;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.posts-container {
	display: flex;
	background-color: silver;
	height: 620px;
}

/* Responsive layout - makes a one column layout instead of a two-column layout */
@media (max-width: 800px) {
	.posts-container {
	  flex-direction: column;
	}
  }

.post-container {
	padding: 15px;
	padding-top: 25px;
}

.post{
	background-color: silver;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	border-radius: 5ch;
}
.image {
	width: 100%;
	height: 200px;
}


